.cargo_wrap {
    display:flex;
    flex-direction:column;
    justify-content: space-between;
    height:100vh;
}
.title {
    display: flex;
    justify-content:space-between;
    align-items:center;
    padding:20px;
    color:#fff;
    font-weight:bold;
    text-align:center;
    text-shadow:2px 2px 5px #000;
    font-size:30px;
    background:#ffffff60;
    border-radius:15px;
}
.title button {
    background:transparent;
    color:#fff;
    border:none;
    font-weight:bold;
    font-size:30px;
    cursor:pointer;
    border-radius:5px;
}
.title button:hover, .title button:active {
    background:#c700fe;
    color:#000;
}
.cart_list_wrap {
    width:100%;
    height:50vh;
    overflow-y: auto;
}
.cart_box {
    width:80%;
    margin:0 auto;
    display:flex;
    flex-wrap:wrap;
    justify-content: center;
}
.planet_list {
    width:20%;
    list-style:none;
    border:1px solid #000;
    border-radius:15px;
    padding:20px;
    margin:20px auto;
    box-shadow:2px 2px 10px #fff;
    background:#000;
    transition:0.5s;
}
.planet_box {
    display:flex;
    justify-content:space-between;
    align-items:center;
    text-align:left;
}
.planet_name {
    color:#c700fe;
}
.planet_price {
    color:#48ff0ff0;
}
.decBtn {
    background:none;
    color:#ff0048;
    font-weight:bold;
    font-size:20px;
    border:none;
    cursor:pointer;
}
.total {
    display: flex;
    justify-content:space-between;
    align-items:center;
    padding:20px;
    color:#fff;
    font-weight:bold;
    text-align:center;
    text-shadow:2px 2px 5px #000;
    font-size:30px;
    background:#ffffff60;
    border-radius:15px;
}