.form_info {
    display:flex;
    flex-direction:column;
}
.form_info_about {
    display:flex;
    justify-content:space-around;
    align-items:center;
}
.form_info_text {
    font-size:18px;
    font-weight:bold;
}
.form_info_input {
    display:inline-block;
    content:"";
    background:#fff;
    width:20px;
    height:20px;
    border:none;
    border-radius:50%;
    margin-left:10px;
    text-align:center;
    font-weight:bold;
}
.form_btn {
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
}
.form_active {
    display:flex;
    justify-content:center;
    align-items:center;
}
.form_inc, .from_dec {
    width:40px;
    height:25px;
    border:none;
    border-radius: 15px;
    margin:10px 0;
    font-size:20px;
    font-weight:bold;
    cursor:pointer;
} 
.form_inc {
    margin-right:10px;
    background:#48ff00;
}
.form_inc:active, .form_inc:hover {
    background:#c700fe;
    color:#fff;
}
.from_dec {
    background:#ff006f;
}
.from_dec:active, .from_dec:hover {
    background:#c700fe;
    color:#fff;
}
.formBtn_confirm {
    border:none;
    width:95px;
    height:30px;
    border-radius:15px;
    background:#c700fe;
    cursor:pointer;
    color:#fff;
    font-weight:bold;;
}
.formBtn_confirm:hover, .formBtn_confirm:active {
    background:#48ff00;
    color:#000;
}