ul {
    display:flex;
    flex-wrap:wrap;
}
.planet_list {
    width:33.33%;
    list-style:none;
    border:1px solid #fff;
    border-radius:15px;
    padding:20px;
    margin:20px auto;
    box-shadow:2px 2px 10px #000;
    background:#000000;
    transition:0.5s;
}
.planet_box {
    display:flex;
    justify-content:space-between;
    align-items:center;
    text-align:left;
}
.planet_name {
    color:#c700fe;
}
.planet_dsc {
    margin:10px 0;
    color:#ffffff70;
}
.planet_price {
    color:#48ff00;
}

@media (max-width:700px) {
    .planet_dsc {
        font-size:12px;
    }
    ul {
        display:block;
        margin:0;
        padding:0;
    }
    .planet_list {
        width:80%;
    }
}