.planets_header_wrap {
    display:flex;
    width:100%;
    margin:0 auto;
    justify-content: space-between;
    align-items:center;
    box-shadow:0 2px 3px #000;
    background:url('../../img/headerImg.jpg');
    background-position:0% 35%;
    background-size:cover;
    background-repeat:no-repeat;
}
.planets_header_wrap h1 {
    width:100%;
    padding-left:10%;
}
.planets_hedaer_right {
    width:30%;
    display:flex;
    justify-content:right;
    align-items:center;
    padding-right:10%;
}
.planets_header_cart {
    display:flex;
    width:50%;
    height:35px;
    margin-right:10px;
    border:2px solid #fff;
    color:#fff;
    border-radius:15px;
    align-items:center;
    justify-content:space-around;
    cursor:pointer;
}
.planets_header_cart:hover, .planets_header_cart:active {
    color:#fff;
    background:#000;
    border:2px solid transparent;
} 

.planet_header_close {
    display:flex;
    align-items:center;
    font-size:30px;
    line-height:25px;
    margin:0;
    padding:0;
    cursor:pointer;
    color:#fff;
}
.planet_header_close:hover, .planet_header_close:active {
    color:#000;

}

@media(max-width:767px){
    .planets_header_cart {
        display:none;
    }
}