.backdrop {
    position: fixed;
    top:0; left:0; width:100%; height:100vh;
    z-index: 20;
    background:rgba(0,0,0,0.7);
  }
.modal {
  position:fixed;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  display:block;
  content:"";
  width:80%;
  height:100%;
  background:#fff;
  z-index:21;
}
.cont_wrap {
  display:flex;
  width:100%;
  height:100vh;
  flex-direction:column;
  justify-content: space-between;
}

@media(max-width:767px){
  .modal {
    height:auto;
  }
  .cont_wrap {
    display:block;
    height:auto;
  }

}