.login_wrap {
    padding:20px;
    text-align:center;
    box-sizing:border-box;
    overflow:hidden;
}
.login_head {
    display:flex;
    justify-content:space-between;
    align-items:center;
}
.login_head button {
    background:none;
    border:none;
    font-weight:bold;
    font-size:20px;
    cursor:pointer;
}
.login_head button:hover, .login_head button:active {
    color:#c700fe;
}

.loginbtns {
    display:flex;
    justify-content:center;
    margin:auto;
    position:relative;
    margin:20px 0;
}
.loginbtns button {
    width:50%;
    border:none;
    height:30px;
    background:none;
    font-size:17px;
    padding-bottom:10px;
    border-radius:15px 15px 0 0;
    cursor:pointer;
}
.button_active {
    background:#eee !important;
    border:1px solid red;
}
.state_bar_right {
    position:absolute;
    bottom:0;
    right:0;
    content:"";
    width:50%;
    height:3px;
    background:#48ff00;
    transition:.4s;
}
.state_bar_left {
    position:absolute;
    bottom:0;
    left:0;
    content:"";
    width:50%;
    height:3px;
    background:#48ff00;
    transition:.4s;
}

.conected_google {
    display:flex;
    justify-content:center;
    align-items:center;
    border:1px solid #c700fe;
    border-radius:20px;
    height:40px;
}
.conected_google div {
    border:none;
    background:none;
    font-size:17px;
}


.login_tap, .signup_tap {
    display:none;
}
.on {
    display:block !important;
}
.login_wrap h4 {
    text-align:left;
    margin-bottom:5px;
}
.user_name, .user_email, .user_password{
    width:100%;
    height:30px;
    outline:none;
    box-sizing:border-box;
    border:1px solid #ccc;
    border-radius:15px;
    outline:none;
    padding-left:10px;
}
.signin_btn {
    border:none;
    width:100px;
    height:30px;
    margin-top:15px;
    border:1px solid #c700fe;
    border-radius:15px;
    background:#fff;
    cursor: pointer;
}
.signin_btn:hover, .signin_btn:active {
    background:#c700fe;
    border:none;
    color:#fff;
}
.login_wrap p {
    color:#ccc;
}
.login_wrap p span {
    color:#666;
}
.login_btn {
    border:none;
    width:100px;
    height:30px;
    margin-top:15px;
    border:1px solid #c700fe;
    border-radius:15px;
    background:#fff;
    cursor: pointer;
}
.login_btn:hover, .login_btn:active {
    background:#c700fe;
    border:none;
    color:#fff;
}


@media(max-width:767px){
    .login_wrap {
        width:100%;
        margin:auto;
    }
    .login_head {
        font-size:14px;
    }
    .loginbtns button {
        font-size:14px;
        height:22px;
    }
    .conected_google div {
        font-size:14px;
    }
    .login_wrap p {
        font-size:12px;
    }
    .login_wrap h4 {
        font-size:14px;
    }
}