.backdrop {
    position: fixed;
    top:0; left:0; width:100%; height:100vh;
    z-index: 20;
    background:rgba(0,0,0,0.7);
  }
.modal {
  position:fixed;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  display:block;
  content:"";
  width:500px;
  height:530px;
  background:#fff;
  border-radius:15px;
  z-index:21;
}

@media(max-width:767px){
  .modal {
    width:80%;
  }
}