.planet_detail_wrap {
    position:fixed;
    top:0;
    bottom:0;
    left:0;
    right:0;
    width:100%;
    height:100vh;
}
.detail_backdrop {
    position:absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
    background:#00000090;
    z-index:23;
}
.detail_modal {
    position:absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;
    display:flex;
    flex-direction:column;
    justify-content: space-between;
    width:70%;
    height: 70%;
    margin:auto;
    border-radius:15px;
    background:#fff;
    z-index:24;
}
.detail_img {
    display:flex;
    justify-content:center;
    align-items:center;
    width:100%;
    height:auto;
    border-radius:15px 15px 0 0;
    overflow:hidden;
}
.detail_img img {
    width:100%;
}
.detail_main_title {
    width:100%;
    text-align:center;
}
.detail_main_title > h2 {
    color:#000;
}
.detail_main_title > p {
    width:80%;
    margin:auto;
    color:#666;
    font-size:14px;
}
.detail_sub_title {
    display:flex;
    justify-content:space-between;
    align-items: center;
    padding:0 10px 10px 10px;
}
.detail_sub_left {
    width:50%;
    display:flex;
    flex-direction:column;
    justify-content:center;
}
.detail_sub_left_state {
    color:#111;
}
.detail_sub_left_kind {
    color:#111;
}
.detail_sub_left_price {
    color:#111;
}
.detail_sub_right {
    display:flex;
    flex-direction:column;
}
.detail_sub_right > button {
    border:1px solid #000;
    border-radius:15px;
    padding:5px 20px;
}
.detail_sub_cart_inc {
    margin-top:10px;
}
.hot {
    background:#f20049;
    color:#fff;
}
.home {
    background:#065900;
    color:#fff;
}
.wind {
    background:#01cdf6;
    color:#000;
}
.cold {
    background:#001555;
    color:#fff;
}
.rock {
    background:#2b2424;
    color:#fff;
}
.gas {
    background:#818801;
    color:#fff;
}

@media(max-width:767px){
    .detail_modal {
        width:85%;
        height:55%;
    }
    .detail_sub_left {
        font-size:14px;
    }
}