.card_case {
    position:relative;
    top:50%;
    left:0;
    width:100%;
    height:800px;
    transform:translate(0, -50%);

}
.card_wrap {
    color:#fff;
    text-align:left;
    position:absolute;
    top:50%;
    left:50%;
    transform:translate(-50%, -50%);
    width:50%;
    height:auto;
    border:1px solid #fff;
    border-radius:15px;
    padding:20px;
    margin:auto;
    background:#0000006b;
    box-shadow:3px 3px 5px #000;
}
.h2 {
    margin:40px 0;
}
.card_text {
    line-height:30px;
    font-size:16px;
}

@media (max-width:767px){
    .card_wrap {
        width:80%
    }
    .h2 {
        margin:10px 0;
    }
    .card_text {
        line-height:30px;
        font-size:14px;
    }
}