.cart_wrap {
    width:200px;
    height:50px;
    display:flex;
    justify-content:space-around;
    align-items:center;
    padding:0 20px;
    color:#fff;
    background:#00000070;
    border-radius:25px;
    box-shadow:4px 2px 5px #000;
    cursor:pointer;
}
.cart_icon {
    width:30px;
    height:30px;
    line-height:30px;
}
.cart_text {
    display:inline-block;
}
.cart_count {
    width:30px;
    border:1px solid #000;
    border-radius:30px;
    background:#00f008;
    color:#000;
}

@media (max-width:767px){
    .cart_wrap {
        display:none;
    }
}