.parent_tag {
    background:#ffffff;
    width:100%;
    height:150px;
    padding-top:10px;
    border-radius:0 0 15px 15px;
    border:1px solid #000;
}
.tag_btns_wrap {
    width:80%;
    margin:auto;
}
.tag_btns_wrap > div {
    width:100%;
    height:30px;
    margin:5px 0;
    font-size:18px;
}
.tag_btns_wrap > div > span {
    font-weight:bold;
}
.tag_btns_wrap > div > button {
    border:2px solid transparent;
    font-size:16px;
    font-weight:bold;
    border-radius:10px;
    margin-right:10px;
    cursor:pointer;
}
.sale{
    color:#ff0000;
    background:#fff;
}
.sale:hover, .sale:active{
    color:#fff;
    background:#ff0000;
}
.sale.selected {
    color:#ff0000;
    background:#fff;
    border:2px solid #ff0000 !important;
}
