.info_content {
    display:flex;
}

.info_content_wrap {
    display:flex;
    flex-direction:column;
    margin:50px auto;
    padding:20px;
    width:50%;
    height:auto;
    color:#fff;
    border-radius:30px;
    background:#00000095;
}
.info_img {
    width:100%;
    height:auto;
    margin:auto;
}
.info_img img {
    width:100%;
    height:100%;
    border-radius:15px;
}

.info_text {
}
.info_text h2 {
    font-size:25px;
}
.info_text p {
    font-size:18px;
}

.info_tag {
}
.info_tag button {
    display:inline-block;
    width:200px;
    height: 50px;
    font-size:20px;
    border:none;
    border-radius:15px;
    cursor:pointer;
    font-weight:bold;
}
.hot {
    background:#f20049;
    color:#fff;
}
.home {
    background:#065900;
    color:#fff;
}
.wind {
    background:#01cdf6;
    color:#000;
}
.cold {
    background:#001555;
    color:#fff;
}
.rock {
    background:#2b2424;
    color:#fff;
}
.gas {
    background:#818801;
    color:#fff;
}
@media(max-width:767px){
    .info_content_wrap {
        width:100%;
    }
    .info_text h2 {
        font-size:20px;
    }
    .info_text p {
        font-size:14px;
    }
    .info_tag button {
        font-size:14px;
        width:100px;
        height: 30px;
    }
}