.planet_content_wrap {
    display:flex;
    width:50%;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    background:#ffffff20;
    padding:10px 0;
}
.planet_content_img:hover, .planet_content_img:active  {
    box-shadow:2px 7px 5px #5a00f6;
    border:1px solid #5a00f6;
}

.planet_content_img {
    width:80%;
    height:80%;
    margin:auto;
    border:1px solid #fff;
    border-bottom:none;
    border-radius:15px 15px 0 0;
    box-shadow:2px 7px 5px #fff;
    cursor:pointer;
}
.planet_content_img img {
    width:100%;
    height:100%;
    border-radius:15px 15px 0 0;
}
.planet_content_top {
    width:80%;
    padding:10px 0;
    background:#000;
    color:#fff;
    border:1px solid #fff;
    border-top:none;
    border-bottom:none;
    box-shadow:2px 6px 5px #fff;
}
.planet_content_img:hover + .planet_content_top, .planet_content_img:active + .planet_content_top  {
    box-shadow:2px 6px 5px #5a00f6;
    border:1px solid #5a00f6;
    border-top:none;
    border-bottom:none;
}
.planet_content_top > p {
    margin:5px 0;
    padding:0 10px;
}
.planet_content_title {
    font-size:18px;
    font-weight:bold;
}
.planet_content_des {
    color:#aaa;
}
.planet_content_bottom {
    width:80%;
    padding:10px 0;
    height:auto;
    display:flex;
    align-items:center;
    justify-content:space-between;
    background:#000;
    border-radius:0 0 15px 15px;
    color:#fff;
    border:1px solid #fff;
    border-top:none;
    box-shadow:2px 5px 5px #fff;
}
.planet_content_img:hover ~ .planet_content_bottom, .planet_content_img:active ~ .planet_content_bottom  {
    box-shadow:2px 5px 5px #5a00f6;
    border:1px solid #5a00f6;
    border-top:none;
}
.planet_content_text {
}
.planet_content_text > p {
    margin:5px 0;
    padding:0 10px;
    color:#fff;
}
.planet_content_state {}
.sale {
    background:#ed041f;
    color:#fff;
}
.soldout {
    background:#014707;
    color:#fff
}
.planet_content_kind {
    font-weight:bold;
}
.planet {
    color:#5a00f6;
}
.asteroid {
    color:#dd0606
}
.dwarf {
    color:#0077ff
}
.moon {
    color:#535a6c
}
.planet_content_price {
    color:#111
}
.planet_content_btn {
    width:30%;
    padding:0 10px;
}
.planet_content_btn button {
    display:inline-block;
    width:100%;
    height: 50px;
    font-size:16px;
    border:none;
    border-radius:10px;
    cursor:pointer;
    font-weight:bold;
}
.hot {
    background:#f20049;
    color:#fff;
}
.home {
    background:#065900;
    color:#fff;
}
.wind {
    background:#01cdf6;
    color:#000;
}
.cold {
    background:#001555;
    color:#fff;
}
.rock {
    background:#2b2424;
    color:#fff;
}
.gas {
    background:#818801;
    color:#fff;
}

@media(max-width:767px){
    .planet_content_wrap {
        width:100%;
    }
}