.header_wrap {
    position:relative;
    width:100%;
    height:100px;
    background:url('../../img/headerImg.jpg');
    background-size:cover;
    background-position: 0 35%;
    box-shadow:0 5px 10px #000;
    z-index:1
}
.header {
    max-width:800px;
    height:100px;
    display:flex;
    justify-content:space-between;
    align-items:center;
    margin:auto;
    padding:0 10px;
}
h1 {
    color:#fff;
    text-shadow:2px 2px 2px #000;
}
.main_visual {
    position:relative;
    display:block;
    content:"";
    width:100%;
    height:800px;
    background:url('../../img/mainVisual.png');
    background-position:50% 85%;
    background-repeat:no-repeat;
    background-size:cover;
    overflow:hidden;
    box-shadow:0 1px 10px #fff;
}
.user_box {
    display:flex;
    justify-content:space-between;
    align-items:center;
}

@media(max-width:767px){
    .header_wrap {
        position:fixed;
    }
    h1 {
        font-size:20px;
    }
}