.user_box {
    display:flex;
    content:"";
    width:100%;
    height:200px;
}
.user_box_info {
    display:flex;
    flex-direction:column;
    content:"";
    width:50%;
    height:100%;
}
.state {
    display:flex;
    justify-content:space-between;
    align-items:center;
    margin-right:10px;
}
.user_box_img {
    width:80px;
    height:80px;
    color:#c700fe;
    background:#fff;
}
.logout {
    border:2px solid transparent;
    border-radius:15px;
    width:80px;
    height:80px;
    background:#fff;
    color:#c700fe;
    font-size:35px;
    cursor:pointer;
}
.logout:hover, .logout:active {
    background:#c700fe;
    color:#fff;
}
.user_box_name, .user_box_email {
    width:80%;
    height:20px;
    margin:20px 0 0 0;
    font-size:20px;
    font-weight:bold;
    color:#000;
    background:#fff;
    padding:5px 5px;
}
.user_box_menu {
    display:flex;
    justify-content:space-around;
    align-items:center;
    content:"";
    width:50%;
    height:100%;
}
.active_btn {
    width:80%;
    height:100%;
}
.active_btn button {
    width:100%;
    height:50px;
    margin-top:10px;
    background:#fff;
    border:2px solid #c700fe;
    border-radius:20px;
    font-size:18px;
    font-weight:bold;
    color:#c700fe;
    cursor:pointer;
}
.active_btn button:hover, .active_btn button:active {
    color:#fff;
    background:#c700fe;

}
.close {
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:flex-start;
}
.close button {
    background:none;
    border:none;
    font-size:30px;
    line-height:30px;
    font-weight:bold;
    color:#000;
    border-radius:10px;
    cursor:pointer;
}
.close button:hover, .close button:active {
    color:#c700fe;
}
.user_box_menu > button:hover, .user_box_menu  > button:active {
    background:#c700fe;
    color:#fff;
}
.user_content {
    display:flex;
    justify-content:space-between;
    align-items:center;
    content:"";
    width: 100%;
    height:300px;
    margin-top:10px;
    text-align:center;
    box-sizing:border-box;
}
.user_content > div {
    width:25%;
    margin:0 10px;
    border-radius:15px;
    padding:10px 0 0 0;
    cursor:pointer;
}
.user_content > div > p {
    font-size:15px;
    font-weight:bold;
}
.about_iocn, .planet_icon, .contect_icon, .support_icon {
    font-size:100px;
}
.about {
    background:#fff;
    border:none;
    color:#ff0051;
    border:2px solid transparent;
}
.about:hover, .about:active {
    color:#fff;
    background:#ff0051;
    border:2px solid #ff0051;
}
.planet {
    background:#fff;
    color:#6f00ff;
    border:2px solid transparent;
}
.planet:hover, .planet:active {
    background:#6f00ff;
    color:#fff;
    border:2px solid #6f00ff;

}
.contect {
    background:#fff;
    color:#035400;
    border:2px solid transparent;
}
.contect:hover, .contect:active {
    background:#035400;
    color:#fff;
    border:2px solid #035400;
}
.support {
    background:#fff;
    color:#f19101;
    border:2px solid transparent;
}
.support:hover, .support:active {
    background:#f19101;
    color:#fff;
    border:2px solid #f19101;
}
.info_footer {
    position:relative;
    bottom:0;
    left:0;
    width:100%;
    height:17vh;
    background:#666;
}
.sns_wrap {
    display:flex;
    width:50%;
    line-height:10vh;
    margin:auto;
    justify-content:center;
    font-size:35px;
    color:#fff;
}
.sns_wrap > div {
    margin:0 10px;
}
.info_copy {
    color:#fff;
    text-align:center;
    font-size:14px;
}
.info_copy p {}

@media(max-width:767px){
    .user_box {
        height:250px;
    }
    .user_box_img {
        width:50px;
        height:50px;
    }
    .user_box_name, .user_box_email {
        font-size:15px;
    }
    .user_box_menu {
        flex-direction:column;
    }
    .active_btn button {
        width:100%;
        font-size:14px;
    }
    .user_content {
        flex-wrap:wrap;
        height:auto;
    }
    .user_content > div {
        width:40%;
    }
    .sns_wrap {
        font-size:25px;
    }
    .info_copy {
        font-size:10px;
    }
    .close {
        display:block;
        width:100%;
    }
    .close button {
        width:100%;
        text-align:right;
        padding-right:12%;
    }
}