.backdrop {
    position: fixed;
    top:0; left:0; width:100%; height:100vh;
    z-index: 20;
    background:rgba(0,0,0,0.7);
  }
.modal {
  position:fixed;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  display:block;
  content:"";
  width:80%;
  height:100%;
  background:url('../../img/CartBg.jpg');
  background-repeat:no-repeat;
  background-size:cover;
  background-position:center;
  border-radius: 20px;
  z-index:22;
}