.planet_info_content {
    display:flex;
    width:100%;
    height:115vh;
    align-items:center;
    background:url('../../img/bgcontent.jpg');
    background-size:cover;
    background-position:center;
    background-repeat:no-repeat;
    box-sizing:border-box;
}
.planet_info_prev, .planet_info_next {
    background:transparent;
    color:#fff;
    width:135px;
    height:60px;
    font-size:60px;
    border:none;
    cursor:pointer;
}
.planet_info_prev svg {
    transform:rotate(-135deg);
}
.planet_info_next svg {
    transform:rotate(45deg);
}
.planet_info_prev:hover, .planet_info_prev:active, .planet_info_next:hover,.planet_info_next:active {
    color:#c700fe
}
.listBox {
    color:#fff;
}
.add_list {
    border:none;
    width:40px;
    height:40px;
    border-radius:40px;
    font-size:20px;
    font-weight:bold;
    background:#c700fe;
    color:#fff;
    cursor:pointer;
    margin:20px 0;
}
.add_list:hover, .add_list:active{
    background:#48ff00;
    color:#000;
}
.hide_add_list {
    display:none;
}

@media(max-width:767px){
    .planet_info_content {
        height:80vh;
    }
    .planet_info_prev, .planet_info_next {
        font-size:40px;
    }

}