.login_wrap {
    width:50px;
    height:50px;
    color:#fff;
    margin-left:30px;
}
.login_icon {
    width:100%;
    height:100%;
    cursor:pointer;
}
.login_wrap:hover, .login_wrap:active {
    color:#c700fe;
}

@media(max-width:767px){
    .login_wrap {
        width:30px;
        height:30px;
    }
}